import style from "../../FinanceManager/PendingFees/PendingFees.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
const ExcessFeesListCard = ({ student }) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();

  const onViewApplicantProfile = () => {
    navigate(`/q/${params.username}/student/profile/admission`, {
      state: {
        // studentId: student?._id
        sid: student?._id,
        viewAs: "ADMISSION_ADMIN",
        openTab: "FEES_DATA",
      },
    });
  };

  return (
    <div
      className={style.pending_fees_card}
      onClick={onViewApplicantProfile}
      title="Refund student fees"
    >
      <div className={style.pending_fees_card_container}>
        <img
          alt="Student Avatar"
          src={
            student?.studentProfilePhoto
              ? `${imageShowUrl}/${student?.studentProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          loading="lazy"
        />
        <div className={style.pending_fees_card_inner}>
          <h6>{`${student?.studentFirstName} ${
            student?.studentMiddleName ? student?.studentMiddleName : ""
          } ${student?.studentLastName}`}</h6>

          <p>
            {t("gr_number")}
            {" : "}
            {student?.studentGRNO ?? ""}
            {student?.department?.dName
              ? `, ${student?.department?.dName}`
              : ""}
          </p>
        </div>
      </div>
      <div className={style.pending_fees_card_inner}>
        <h6>
          {t("rs_only")} {student?.studentRemainingFeeCount}
        </h6>
        <p>{t("excess_fees")}</p>
      </div>
    </div>
  );
};

export default ExcessFeesListCard;
